.p-career-item {
  padding: 6rem 0 4rem;
  background-color: #fff;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.15);
}

@media screen and (max-width: 56em) {
  .p-career-item {
    padding: 3rem 0;
  }
}

@media screen and (max-width: 56em) {
  .p-career-item__inner {
    padding: 0 20px;
  }
}

.p-career-item__name {
  margin-bottom: 3rem;
  height: 8rem;
  text-align: left;
}

@media screen and (max-width: 56em) {
  .p-career-item__name {
    margin-bottom: 2rem;
  }
}

@media screen and (max-width: 23em) {
  .p-career-item__name {
    height: auto;
  }
}

.p-career-item__name img {
  width: auto;
  height: 100%;
}

@media screen and (max-width: 23em) {
  .p-career-item__name img {
    width: 100%;
    height: auto;
  }
}

@media screen and (max-width: 56em) {
  .p-career-item__thumb {
    height: 51.2vw;
    overflow: hidden;
    text-align: center;
  }
}

@media screen and (max-width: 56em) {
  .p-career-item__thumb img {
    margin-left: 50%;
    transform: translateX(-50%);
    height: 100%;
    width: auto;
  }
}

.p-career-item__txt h3 {
  margin: 4rem 0 2rem;
  color: #00AEC4;
  font-size: 2rem;
  line-height: calc(28/20);
  font-weight: 500;
}

@media screen and (max-width: 56em) {
  .p-career-item__txt h3 {
    margin: 3rem 0 1.5rem;
  }
}

.p-career-item__txt p {
  margin-top: 0;
  letter-spacing: .1em;
}

@media screen and (max-width: 56em) {
  .p-career-item__txt p {
    letter-spacing: .095em;
    margin-bottom: 2.8rem;
  }
}

.p-career-item__table {
  margin-top: 4rem;
}

.p-career-item__btn {
  margin-top: 4rem;
  text-align: center;
}

@media screen and (max-width: 56em) {
  .p-career-item__btn {
    margin-top: 3rem;
  }
}

.p-career-item__btn .btn {
  max-width: 240px;
}

.career-list {
  padding: 10rem 0 16rem;
}

@media screen and (max-width: 56em) {
  .career-list {
    padding: 6rem 0 10rem;
  }
}

@media screen and (max-width: 56em) {
  .career-list__inner {
    padding: 0 20px;
  }
}

.career-list__title {
  margin-bottom: 5rem;
}

@media screen and (max-width: 56em) {
  .career-list__title {
    margin-bottom: 4rem;
  }
}

.career-list__body .p-career-item {
  margin-top: 8rem;
}

@media screen and (max-width: 56em) {
  .career-list__body .p-career-item {
    margin-top: 4rem;
  }
}

.career-list__body .p-career-item:first-child {
  margin-top: 0;
}

.career_form-content {
  padding: 10rem 0 16rem;
}

@media screen and (max-width: 56em) {
  .career_form-content {
    padding: 6rem 0 10rem;
  }
}

@media screen and (max-width: 56em) {
  .career_form-content__inner {
    padding: 0 10px;
  }
}
